import './App.css';
import { Route, Switch } from 'react-router-dom';
import Home2 from './Pages/Home2';
import Profile1 from './Pages/Profile1';
import MakeProfile from './Pages/MakeProfile';
import ProfilePicture from './Pages/ProfilePicture';
import Preferences from './Pages/Preferences';

function App() {
  return (
    <div className="App">

      {/* Router - Pages */}
      <Switch>
        <Route path={process.env.PUBLIC_URL + '/'} exact > <Home2 /> </Route>
        <Route path={process.env.PUBLIC_URL + '/home2'}  > <Home2 /> </Route>
        <Route path={process.env.PUBLIC_URL + '/profile1'}  > <Profile1 /> </Route>
        <Route path={process.env.PUBLIC_URL + '/Makeprofile'}  > <MakeProfile /> </Route>
        <Route path={process.env.PUBLIC_URL + '/ProfilePicture'}  > <ProfilePicture /> </Route>
        <Route path={process.env.PUBLIC_URL + '/Preferences'}  > <Preferences /> </Route>
      </Switch>

    </div>
  );
}

export default App;
