import React from "react";
import ChooseTypes from "../Components/Preferences/ChooseType";
import SmallChips from "../Components/Preferences/Chip1";
import { Link, Router } from "react-router-dom";
import Grid from '@material-ui/core/Grid';
import Edit from "../Components/Preferences/Edittext";
import Arrow from "../Components/Preferences/Arrow";
import ContinueButton from "../Components/Preferences/Continue";

export default function Preferences() {
    return (
        <React.Fragment>
            <Edit />
            <Grid container spacing={12}
                justifyContent="centre"
                alignItems="centre"
            >
                <Grid item xs={1} sm={3} md={4}>
                    <Link to={process.env.PUBLIC_URL + '/profilepicture'}>
                        <Arrow />
                    </Link>
                </Grid>
                <Grid item xs={11} sm={9} md={5}>
                    <ChooseTypes />
                </Grid>
            </Grid>
            <Grid container spacing={12}
                direction="column"
                justifyContent="centre"
                alignItems="centre"
            >
                <Grid item xs={12} sm={12} md={12}>
                    <SmallChips />
                </Grid>
                <Grid item xs={12} sm={12} md={12}>
                    <ContinueButton />
                </Grid>
            </Grid>
        </React.Fragment>

    );
}