import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    '& > *': {
      margin: theme.spacing(5),
      padding: theme.spacing(2),
    },
  },
}));

export default function SmallChips() {
  const classes = useStyles();

  const handleDelete = () => {
    console.info('You deselected the delete icon.');
  };

  const handleClick = () => {
    console.info('You selected the Chip.');
  };

  return (
    <div className={classes.root}>
      <Grid container spacing={2}
        justifyContent="flex-start"
        alignItems="centre"
      >
        <Grid item xs={4} sm={3} md={3}>
          <Chip
            size="small"
            label="Thriller"
            onClick={handleClick}
            color="primary"
            onDelete={handleDelete}
          />
        </Grid>
        <Grid item xs={4} sm={3} md={3}>
          <Chip
            size="small"
            label="Drama"
            onClick={handleClick}
            onDelete={handleDelete}
          />
        </Grid>
        <Grid item xs={4} sm={3} md={3}>
          <Chip
            size="small"
            label="Comedy"
            onClick={handleClick}
            color="primary"
            onDelete={handleDelete}
          />
        </Grid>
        <Grid item xs={4} sm={3} md={3}>
          <Chip
            size="small"
            label="Reality"
            onClick={handleClick}
            onDelete={handleDelete}
          />
        </Grid>
        <Grid item xs={4} sm={3} md={3}>
          <Chip
            size="small"
            label="Fiction"
            onClick={handleClick}
            onDelete={handleDelete}
          />
        </Grid>
        <Grid item xs={1} sm={3} md={3}>
          <Chip
            size="small"
            label="Romantic"
            onClick={handleClick}
            color="primary"
            onDelete={handleDelete}
          />
        </Grid>
        <Grid item xs={6} sm={3} md={3}>
          <Chip
            size="small"
            label="Mythology"
            onClick={handleClick}
            onDelete={handleDelete}
          />
        </Grid>
        <Grid item xs={3} sm={3} md={3}>
          <Chip
            size="small"
            label="Paranomal"
            onClick={handleClick}
            onDelete={handleDelete}
          />
        </Grid>
      </Grid>
    </div >
  );
}
