import React from "react";
import Types from "../Components/Profile1/Typography";
import Icons from "../Components/Profile1/Plus";
import TypographyTheme from "../Components/Profile1/Addtype";
import TypographyEdit from "../Components/Profile1/EditType";
import MediaCard from "../Components/Profile1/MediaCard";
import Grid from '@material-ui/core/Grid';

export default function Profile1() {
    return (
        <React.Fragment>
            <TypographyEdit />
            <Grid container spacing={12}
                direction="column"
                justifyContent="flex-end"
                alignItems="center"
            >
                <Grid item xs={12} sm={5} md={12}>
                    <Types />
                </Grid>
                <Grid item xs={12} sm={5} md={12}>
                    <Icons />
                </Grid>
                <Grid item xs={4} sm={5} md={12}>
                    <TypographyTheme />
                </Grid>
            </Grid>
            <footer>
                <MediaCard />
            </footer>
        </React.Fragment>
    );
}