import React from "react";
import Types from "../Components/MakeProfilePage/Typo";
import SvgIconsSize from "../Components/MakeProfilePage/IconProfile";
import ContinueButtons from "../Components/MakeProfilePage/Typo2";
import { Link } from "react-router-dom";
import Icons from "../Components/MakeProfilePage/BackArrow";
import EditIcon from "../Components/MakeProfilePage/EditIcon";
import InputWithIcon from "../Components/MakeProfilePage/Text";
import Grid from '@material-ui/core/Grid';
import TypographyEdit from "../Components/MakeProfilePage/Edit";


export default function MakeProfile() {
    return (
        <React.Fragment>
            <TypographyEdit />
            <Grid container spacing={3}
                justifyContent="flex-start"
                alignItems="center"
            >
                <Grid item xs={1} sm={1} md={1}>
                    <Link to={process.env.PUBLIC_URL + '/profile1'}>
                        <Icons />
                    </Link>
                </Grid>
                <Grid item xs={6} sm={12} md={10}>
                    <Types />
                </Grid>
            </Grid>
            <Grid container spacing={12}
                direction="column"
                justifyContent="centre"
                alignItems="center"
            >
                <Grid item xs={12} sm={12} md={12}>
                    <SvgIconsSize />
                    <EditIcon />
                </Grid>
                <Grid item xs={12} sm={12} md={12}>
                    <InputWithIcon />
                </Grid>
                <Grid item xs={12} sm={12} md={12}>
                    <ContinueButtons />
                </Grid>
            </Grid>
        </React.Fragment>
    );
}